'use strict';

/**
* Evaluate the probability density function (PDF) for a Kumaraswamy's double bounded distribution.
*
* @module @stdlib/stats-base-dists-kumaraswamy-pdf
*
* @example
* import pdf from '@stdlib/stats-base-dists-kumaraswamy-pdf' ;
*
* var y = pdf( 0.5, 1.0, 1.0 );
* // returns 1.0
*
* y = pdf( 0.5, 2.0, 4.0 );
* // returns ~1.688
*
* @example
* import { factory as factory } from '@stdlib/stats-base-dists-kumaraswamy-pdf' ;
*
* var pdf = factory( 0.5, 0.5 );
*
* var y = pdf( 0.8 );
* // returns ~0.86
*
* y = pdf( 0.3 );
* // returns ~0.679
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
